.shopping-popper {
    min-width: 300px;
    width: auto;
    height: auto;
    background-color: #111;
    right: 0;
    top: 3.7rem;
    border-radius: 0.2rem;
    -webkit-box-shadow: -1px 1px 6px 2px rgba(62, 62, 62, 1);
    -moz-box-shadow: -1px 1px 6px 2px rgba(62, 62, 62, 1);
    box-shadow: -1px 1px 6px 2px rgba(62, 62, 62, 1);
    .content {
        display: flex;
        font-family: 'gameheaven-regular';
        font-size: 16px;
        padding: 0.7rem;
        width: 100%;
        height: 100%;
        .no-items-display {
            display: none;
            margin: 2rem auto;
        }
        .main-container {
            display: block;
            width: 100%;
            height: 100%;
            .items-container {
                padding: 0.5rem;
                width: 100%;
                height: 100%;
                max-height: 32rem;
                overflow-y: auto;
                scroll-behavior: smooth;
            }
            .details-container {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0 0.7rem;
                .cart-btn {
                    font-size: 1em;
                    padding: 0.5rem 1.5rem;
                    border-radius: 0.3rem;
                    &:hover {
                        color: #fff;
                        background-color: #ffffffb8;
                    }
                    a {
                        text-decoration: none;
                        color: inherit;
                    }
                }
            }
        }
    }
    .items-container .item {
        padding: 0.6rem;
        border-radius: 0.3rem;
        display: flex;
        gap: 0.7rem;
        &:hover {
            background-color: #3e3e3e70;
        }
        &:active {
            background-color: #3e3e3e8c;
        }
        & > *:not(.imgHover, .info) {
            margin-right: 0.7rem;
            margin: auto;
        }
        .imgHover {
            position: relative;
            display: flex;
            align-items: center;
            &:hover > span {
                transition: all 300ms;
                visibility: visible;
                background-color: #111111d4;
                border-radius: 0.5rem;
                cursor: pointer;
            }
            span {
                display: inline-flex;
                visibility: hidden;
                width: 100%;
                height: 100%;
                border-radius: 0.5rem;
                position: absolute;
                align-items: center;
                justify-content: center;
            }
        }
        .info {
            display: flex;
            flex-direction: column;
            a,
            .itemName {
                font-size: 1em;
                color: #fff;
                text-decoration: none;
                height: min-content;
                width: 20rem;
                // text-overflow: ellipsis;
                // overflow: hidden;
                // white-space: nowrap;
                &:hover {
                    text-decoration: underline;
                }
            }
            span {
                font-size: 0.8em;
            }
            .original-price {
                color: #70ff80;
                margin: 0.3rem 0;
            }
            .sale-price {
                display: none;
            }
            .sale-perc {
                background-color: #94063a;
                //background-color: #8c66ff;
                margin: 0 1rem;
                padding: 0 0.3rem;
                color: #ffce5c;
                border-radius: 0.2rem;
                font-weight: bold;
            }
        }
        &.--sale {
            .original-price {
                text-decoration: line-through !important;
                color: #ffb0b0 !important;
            }
            .sale-price {
                display: block !important;
                color: #70ff80;
            }
        }
        img {
            height: 9rem;
            width: 16rem;
            object-fit: contain;
            object-position: center;
            font-size: 1rem;
        }
        .value {
            min-width: 5em;
            width: auto;
            text-align: center;
        }
    }
    &.--no-items {
        .no-items-display {
            display: block;
        }
        .main-container {
            display: none;
        }
    }
}
.mediaReview-popper {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #111111;
    .imgContainer {
        position: absolute;
        top: 0;
        display: flex;
        width: 100%;
        height: 100%;
    }
    img {
        margin: auto;
        max-width: 78vw;
        max-height: 100vh;
        image-rendering: pixelated;
    }
    .toolContainer {
        position: relative;
        margin: 2rem;
        z-index: 1;
        & > .toolRight {
            float: right;
            & > .fontIcon {
                margin-right: 1rem;
            }
        }
    }
    .fontIcon {
        padding: 0.3rem;
        border-radius: 100%;
        cursor: pointer;
        &.--disable {
            color: #9aa0a6;
            cursor: not-allowed;
            background: none !important;
        }
        &:hover {
            background-color: #90909064;
        }
        &:active {
            background-color: #90909064;
        }
    }
}
.profilePopper {
    width: max-content;
    height: auto;
    background-color: #111;
    right: 0;
    top: 3.7rem;
    border-radius: 1rem;
    -webkit-box-shadow: -1px 1px 6px 2px rgba(62, 62, 62, 1);
    -moz-box-shadow: -1px 1px 6px 2px rgba(62, 62, 62, 1);
    box-shadow: -1px 1px 6px 2px rgba(62, 62, 62, 1);
    padding: 1rem;
    .profile__nav {
        color: #fff;
        padding: 0.5rem 1rem;
        margin: 0.5rem 0;
        font-family: 'gameheaven-regular';
        font-size: 1.6rem;
        display: flex;
        align-items: center;
        border-radius: 0.5rem;
        &:hover {
            background-color: #ffffff44;
        }
    }
    .nav__icon {
        background-color: #ffffff35;
        border-radius: 100%;
        padding: 0.6rem;
    }
    .nav__title {
        display: inline-flex;
        align-items: center;
        width: 100%;
        margin: 0 1rem;
        img {
            width: 3rem;
            margin: 0 1rem;
            image-rendering: optimizeQuality;
        }
    }
}
